<template>
   <div id="side-panel">
      <el-collapse v-model="activePanel" accordion>
         <el-collapse-item name="toolbar">
            <template slot="title">
               <span>Панель компонентов</span>
            </template>
            <draggable
               :list="components"
               :options="{
                  sort: false,
                  group: {
                     name: 'list',
                     pull: 'clone',
                     put: false
                  }
               }"
               :clone="(original)=> JSON.parse(JSON.stringify(original))"
            >
               <el-card
                  class="box-card toolbar-item"
                  v-for="(item, index) of components"
                  :key="index"
                  shadow="hover"
               >
                  {{item.name}}
               </el-card>
            </draggable>
         </el-collapse-item>
         <el-collapse-item name="user-component">
            <template slot="title">
               <span>Пользовательские компоненты</span>
            </template>
            <draggable
               :list="userComponents"
               :options="{
                  group: {
                     name: 'list',
                     pull: 'clone',
                     put: false
                  }
               }"
               :clone="(original)=> JSON.parse(JSON.stringify(original))"
            >
               <el-card
                  class="box-card toolbar-item"
                  v-for="(item, index) of userComponents"
                  :key="index"
                  shadow="hover"
               >
                  {{item.name}}
                  <el-button 
                     type="danger" 
                     icon="el-icon-delete" 
                     size="mini" 
                     circle
                     @click.prevent="removeComp(index)"
                  />
               </el-card>
            </draggable>
            
         </el-collapse-item>
      </el-collapse>
   </div>
</template>

<script>
import draggable from 'vuedraggable'
import { mapGetters, mapMutations } from 'vuex'

export default {
   components: {
      draggable
   },
   data:()=>({
      activePanel: 'toolbar'
   }),
   computed: {
      ...mapGetters({
         components: 'getComponents',
         userComponents: 'getUserComponents',
         test: 'getRootValue'
      })
   },
   methods: {
      ...mapMutations({
          removeComp: 'delUserComponents'
      })
   }
}
</script>

<style lang="scss" scoped>

   #side-panel {
      min-width: 400px;
      max-width: 400px;
      background-color: #fff;
      box-sizing: border-box;
   }

   .toolbar {
      background-color: #b3b3b3;
      &-item {
         margin: 5px;
      }
   }
</style>